// 背景图
.bg-normal(@img, @ext: 'png', @size: 100% 100%, @position: center, @repeat: no-repeat) {
    background: @position ~'/' @size @repeat url('@{img}');
}

@keyframes shipin {
    0% {
        opacity: 0;
    }

    40% {
        opacity: 1;
        transform: scale3d(1, 1, 1);
    }

    100% {
        transform: translate3d(-500px, -700px, 0) scale3d(0.5, 0.5, 0.5);
    }
}

body {
    position: relative;
    height: 100vh;
    // background: linear-gradient(to bottom, transparent 500px, #92511e) !important;
}

#root {
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    // top: constant(safe-area-inset-bottom); /* iOS 11.0 */
    // top: env(safe-area-inset-bottom); /* iOS 11.0 */
}

::-webkit-scrollbar { // 直接复制黏贴到样式页.css，完美解决
    display: none;

    /* background-color:transparent; */
}

.m-main-rank {
    width: 100%;
    height: 579px;
    .bg-normal('./img/bg.png');
    position: relative;
}

.top {
    width: 100%;
    height: 82.5px;
    position: relative;
    overflow: hidden;
    .tit {
        width: 64px;
        height: 16.5px;
        .bg-normal('./img/tit.png');
        margin: 0 auto;
        margin-top: 20.5px;
    }
    .back {
        width: 32px;
        height: 32px;
        .bg-normal('./img/back.png');
        position: absolute;
        top: 16.5px;
        left: 14px;
    }
}

.list-box {
    width: 100%;
    padding: 0 12.5px;
    box-sizing: border-box;
    height: 436px;
    overflow-y: auto;
    margin-top: 16px;
}

.list {
    width: 100%;
}

.item {
    padding-top: 11.5px;
    padding-bottom: 13px;
    border-bottom: 2px solid #3B59EF;
    display: flex;
    align-items: center;
    .rank {
        margin-left: 3px;
        font-size: 13px;
        color: #44FFEF;
        width: 27.5px;
        height: 19px;
        line-height: 18px;
    }
    .user-icon {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-left: 14.5px;
    }
    .username {
        width: 98px;
        height: 20px;
        line-height: 20px;
        margin-left: 8px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #80E1FD;
        font-size: 14px;
    }
    .num {
        margin-left: 47px;
        color: #88FFFF;
        font-size: 12px;
    }
}

.item:nth-child(1) .rank {
    font-size: 0;
    .bg-normal('./img/one.png');
}
.item:nth-child(2) .rank {
    font-size: 0;
    .bg-normal('./img/two.png');
}
.item:nth-child(3) .rank {
    font-size: 0;
    .bg-normal('./img/three.png');
}
